export const ValidarNit = (txtN) => {
    txtN = txtN.toUpperCase();
    if (txtN == "CF" || txtN == "C/F") return true;
    let nit = txtN;
    let pos = nit.indexOf("-");

    if (pos < 0) {
        let correlativo = txtN.substr(0, txtN.length - 1);
        correlativo = correlativo + "-";

        let pos2 = correlativo.length - 2;
        let digito = txtN.substr(pos2 + 1);
        nit = correlativo + digito;
        pos = nit.indexOf("-");
        txtN = nit;
    }

    let Correlativo = nit.substr(0, pos);
    let DigitoVerificador = nit.substr(pos + 1);
    DigitoVerificador.toUpperCase();
    let Factor = Correlativo.length + 1;
    let Suma = 0;
    let Valor = 0;
    for (let x = 0; x <= (pos - 1); x++) {
        Valor = eval(nit.substr(x, 1));
        let Multiplicacion = eval(Valor * Factor);
        Suma = eval(Suma + Multiplicacion);
        Factor = Factor - 1;
    }
    let xMOd11 = 0;
    xMOd11 = (11 - (Suma % 11)) % 11;
    let s = xMOd11;
    if ((xMOd11 == 10 && DigitoVerificador == "K") || (s == DigitoVerificador)) {
        return true;
    }
    else {
        return false;
    }

}

